li {
	padding-bottom: 0.75em;
}

@keyframes aboutFadeIn {
	from {
		transform: scale3d(1, 1, 1);
		opacity: 0;
	}
	30% {
		transform: scale3d(1.25, 0.75, 1);
	}
	40% {
		transform: scale3d(0.75, 1.2, 1);
	}
	50% {
		transform: scale3d(1.15, 0.85, 1);
		opacity: 1;
	}
	65% {
		transform: scale3d(0.95, 1.05, 1);
	}
	75% {
		transform: scale3d(1.05, 0.95, 1);
	}
	to {
		transform: scale3d(1, 1, 1);
		opacity: 1;
	}
}
.easeInOut {
	opacity: 50;
	animation: aboutFadeIn 1s ease-in-out forwards;
	color: black;
}
#lettersRules {
	animation-delay: 0.5s;
}
#lettersrUles {
	animation-delay: 0.55s;
}
#lettersruLes {
	animation-delay: 0.61s;
}
#lettersrulEs {
	animation-delay: 0.75s;
}
#lettersruleS {
	animation-delay: 0.8s;
}
.work-container {
	margin-top: 21rem;
	padding: 0.5rem 15 3.5rem 15rem;
	margin-bottom: 23.5rem;
	min-height: 100vh;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	color: black;
	overflow-x: hidden;
}
.port-wrapper {
	color: black;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	width: 100%;
}
@keyframes portFade {
	100% {
		opacity: 1;
	}
}
#stratary-title {
	font-family: Verdana, sans-serif;
	font-family: Courier New, monospace;
	color: black;
	font-size: 6.25rem;
	transform: scale(0.9);
	animation: portDescScale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
/* h1 */
.port-header {
	text-align: center;
	color: black;
	margin: auto;
	margin-bottom: 0em;
	width: 100%;
	font-size: 6.25rem;
	transform: scale(0.9);
	animation: portDescScale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
/* h2 */
.port-header-2 {
	text-align: left;
	color: black;
	margin: auto;
	width: 75%;
	font-size: 3.25rem;
	transform: scale(0.9);
	animation: portDescScale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
.port-header span {
	display: inline-block;
	transition: color 0.35s ease;
}
.port-header span:hover {
	color: darkblue;
	animation: contactBounce 1s ease forwards;
}
.port-header-2 span {
	display: inline-block;
	transition: color 0.35s ease;
}
.port-header-2 span:hover {
	color: darkblue;
	animation: contactBounce 1s ease forwards;
}
.port-description {
	color: black;
	margin: auto;
	line-height: 1.3em;
	width: 75%;
	font-size: 1.85rem;
	transform: scale(0.9);
	animation: portDescScale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}

@keyframes portDescScale {
	100% {
		transform: scale(1);
	}
}

.port-description span {
	display: inline-block;
	opacity: 0;
	filter: blur(2px);
	padding-right: 1px;
}
.port-description span:nth-child(1) {
	animation: descFadeIn 0.8s 0.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(2) {
	animation: descFadeIn 0.8s 0.8s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(3) {
	animation: descFadeIn 0.8s 0.9s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(4) {
	animation: descFadeIn 0.8s 1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(5) {
	animation: descFadeIn 0.8s 1.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(6) {
	animation: descFadeIn 0.8s 1.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(7) {
	animation: descFadeIn 0.8s 1.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(8) {
	animation: descFadeIn 0.8s 2.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
	color: darkblue;
	text-decoration: underline;
	cursor: pointer;
}
.port-description span:nth-child(9) {
	animation: descFadeIn 0.8s 2.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(10) {
	animation: descFadeIn 0.8s 2.5s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(11) {
	animation: descFadeIn 0.8s 2.6s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(12) {
	animation: descFadeIn 0.8s 2.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.port-description span:nth-child(13) {
	animation: descFadeIn 0.8s 2.8s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
@keyframes descFadeIn {
	100% {
		opacity: 1;
		filter: blur(0);
	}
}

#port-contact-slide span {
	color: darkblue;
	text-decoration: underline;
	cursor: pointer;
}

/* BREAK POINTS */

@media (max-width: 1400px) {
	.other-items {
		grid-template-rows: auto auto auto auto;
		grid-template-columns: auto auto auto;
	}
}
@media (max-width: 1250px) {
	.work-container {
		padding: 0rem 0 3.5rem 0rem;
	}
	.port-items {
		width: 93.5%;
		margin-right: -1rem;
	}
	.port-title {
		font-size: 3.5rem;
	}
}
@media (max-width: 1100px) {
	.work-container {
		padding: 2rem 0 3.5rem 0;
		margin-top: 0;
	}
}
@media (max-width: 950px) {
	.port-header {
		font-size: 4.8rem;
	}
}
@media (max-width: 800px) {
	#stratary-title {
		text-align: center;
		padding: 0em;
	}
	.port-header {
		font-size: 4rem;
	}
	.port-header-2 {
		font-size: 2.5rem;
	}
	/* .ending-work-this-wrapper {
		padding: 0 2.5rem;
		margin-top: 3.5rem;
		text-align: center;
	} */
	.ending-work-this span:first-of-type {
		left: -1.75rem;
	}
}
@media (max-width: 700px) {
	#stratary-title {
		font-size: 4.5rem;
		margin-top: 1em;
		text-align: center;
	}
	.port-header {
		word-wrap: normal;
		font-size: 3.5rem;
	}
	.other-description {
		margin-top: 2rem;
		text-align: center;
		padding: 0 1rem;
	}
	.other-items {
		grid-template-rows: auto auto auto auto auto auto;
		grid-template-columns: auto;
		margin-top: 3rem;
	}
}
@media (max-width: 480px) {
	#stratary-title {
		font-size: 4rem;
	}
	.port-header {
		font-size: 4.5rem;
	}
	.other-header {
		font-size: 4.35rem;
	}
	.port-description {
		font-size: 1.5rem;
		/* padding: 0 2rem; */
		margin-top: 2rem;
	}
	.other-description {
		font-size: 1.65rem;
	}
	.port-wrapper {
		margin-left: -0.75rem;
	}
	.port-items {
		margin-top: 2.5rem;
	}
	.port-item {
		height: 42.5rem;
	}
	@keyframes portAnimItem {
		60% {
			transform: scale(0.9);
		}
		100% {
			width: 35rem;
			transform: scale(1);
		}
	}
}
@media (max-width: 360px) {
	.port-wrapper {
		margin-left: -2rem;
	}
}
