.about-container {
	padding-top: 22.5rem;
	margin: 0 auto;
	width: 75%;
}
.about-left-box {
	width: 100%;
}
.about-left-title {
	position: relative;
	font-size: 15.75rem;
	line-height: 8.25rem;
}
.about-left-title span {
	display: inline-block;
	transition: color 0.35s ease;
}
.about-left-title span:hover {
	color: darkblue;
}
@keyframes aboutFadeIn {
	from {
		transform: scale3d(1, 1, 1);
		opacity: 0;
	}
	30% {
		transform: scale3d(1.25, 0.75, 1);
	}
	40% {
		transform: scale3d(0.75, 1.2, 1);
	}
	50% {
		transform: scale3d(1.15, 0.85, 1);
		opacity: 1;
	}
	65% {
		transform: scale3d(0.95, 1.05, 1);
	}
	75% {
		transform: scale3d(1.05, 0.95, 1);
	}
	to {
		transform: scale3d(1, 1, 1);
		opacity: 1;
	}
}
.easeInOut {
	opacity: 0;
	animation: aboutFadeIn 1s ease-in-out forwards;
	color: black;
}
#letterStratary {
	animation-delay: 0s;
}
#lettersTratary {
	animation-delay: 0.05s;
}
#letterstRatary {
	animation-delay: 0.1s;
}
#letterstrAtary {
	animation-delay: 0.25s;
}
#letterstraTary {
	animation-delay: 0.3s;
}
#letterstratAry {
	animation-delay: 0.35s;
}
#letterstrataRy {
	animation-delay: 0.45s;
}
#letterstratarY {
	animation-delay: 0.5s;
}

@keyframes aboutTitleAfterFade {
	100% {
		opacity: 1;
	}
}

@keyframes aboutDescriptionFade {
	100% {
		opacity: 1;
	}
}

.about-left-description {
	position: relative;
	color: black;
	font-size: 2.6rem;
	letter-spacing: 4px;
	margin-top: 2em;
	animation: aboutDescriptionFade 1s ease 0.65s forwards;
	opacity: 0;
}

@keyframes aboutButtonBoxUp {
	100% {
		margin-top: 6.5rem;
		margin-bottom: 0;
		opacity: 1;
	}
}
.about-left-button-box {
	display: flex;
	align-items: center;
	justify-content: left;
	margin-top: 11.5rem;
	margin-bottom: -5rem;
	opacity: 0;
	animation: aboutButtonBoxUp 1s ease 1.25s forwards;
	padding-right: 10rem;
}
.about-contact-button {
	font-weight: 700;
	position: relative;
	display: block;
	font-size: 18px;
	line-height: 50px;
	letter-spacing: 4px;
	height: 51.5px;
	width: 215px;
	background-color: transparent;
	border: 1px solid black;
	border-radius: 1vw;
	color: black;
	text-align: center;
	cursor: pointer;
	overflow: hidden;
	transition: 0.3s ease-out all;
}
.about-contact-button:hover {
	color: darkblue;
}
.about-contact-button:after {
	background: lightblue;
	color: darkblue;
	content: '';
	height: 220px;
	left: -350px;
	position: absolute;
	top: -100px;
	transform: rotate(-45deg);
	transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
	width: 350px;
	z-index: -10;
}
.about-contact-button:hover:after {
	left: -50px;
}

/* RESPONSIVE */
@media (max-width: 1400px) {
	.about-left-title {
		font-size: 13rem;
		line-height: 7rem;
	}
}
@media (max-width: 1300px) {
	.about-left-title {
		font-size: 12rem;
		line-height: 6.65rem;
	}
	.about-right-title {
		font-size: 11rem;
		line-height: 12rem;
	}
}
@media (max-width: 1200px) {
	.about-left-title {
		font-size: 12rem;
		line-height: 6rem;
	}
	.about-left-description {
		font-size: 1.5rem;
	}
}
@media (max-width: 1100px) {
	.about-left-title {
		font-size: 12rem;
		line-height: 6rem;
	}
	.about-container {
		margin-top: 6em;
		padding: 3.5rem 0 5rem 2rem;
	}
	.about-right-box {
		padding-right: 5rem;
	}
}
@media (max-width: 950px) {
	.about-left-title {
		font-size: 9rem;
		line-height: 8.25rem;
	}
	.about-container {
		align-items: center;
		justify-content: center;
	}
	.about-left-box {
		width: 100%;
	}
	.about-container {
		flex-direction: column;
	}
	.about-left-box {
		padding-left: 0rem;
		font-size: 10px;
	}
}
@media (max-width: 650px) {
	.about-left-button-box {
		padding-right: 0;
	}
	.about-left-box {
		/* margin: 1em 0; */
		font-size: 12px;
	}
	.about-container {
		min-height: 0;
	}
	.about-left-title {
		font-size: 6.5rem;
		line-height: 6rem;
	}
	.about-left-description {
		font-size: 1.5rem;
	}
	.about-contact-button {
		font-size: 15px;
		line-height: 45px;
		height: 45px;
		width: 165px;
	}
}
@media (max-width: 480px) {
	.about-left-title:before {
		margin-top: -3.35rem;
	}
	.about-left-title {
		font-size: 5rem;
		line-height: 4.5rem;
	}
	.about-left-title:after {
		margin-top: 1.25rem;
		margin-left: 0.5rem;
	}
	.about-left-description {
		font-size: 1.35rem;
		letter-spacing: 2px;
	}
	.about-contact-button {
		font-size: 11px;
		line-height: 42px;
		letter-spacing: 2px;
		height: 43px;
		width: 140px;
	}
}
@media (max-width: 390px) {
	.about-right-box {
		width: 100%;
	}
	.about-left-box h1 {
		font-size: 3rem;
		padding-left: 0;
	}
	.about-left-title:before {
		margin-top: -3.125rem;
	}
	.about-left-title {
		font-size: 3.75rem;
		line-height: 4.5rem;
	}
	.about-left-title:after {
		margin-top: 1.35rem;
	}
	.about-left-description {
		font-size: 1.25rem;
		width: 80%;
		padding-left: 0.2rem;
		padding-right: 0;
		margin-right: 0;
	}
	.about-contact-button {
		font-size: 10px;
		line-height: 38px;
		align-self: start;
	}
	.about-left-button-box {
		display: inline-block;
		flex-direction: column;
		align-items: left;
	}
}
